import React from 'react';
import Clock from './Clock.js';
import { Form, Col, Icon, Input, Button} from 'antd';
import history from './history';
import message from 'antd/lib/message';
import Modal from 'antd/lib/modal';

const FormItem = Form.Item;
const styles={
  loginbox:{
    margin:"auto",
    marginTop:150,
    width:300
  }
}

class LoginForm extends React.Component {
  constructor() {
      super();
      this.state = {
          names: [],
          visible:false
      };
  }

  //
  changeName(event){
    let name=event.target.value;
    this.setState({
      name:name,
      userState:null,
      userMsg:null
    })
  }
  //
  changePassword(event){
    let pas=event.target.value;
    this.setState({
      password:pas,
      passState:null,
      passMsg:null
    })
  }
  //
  submit(){//
    // if (this.state.ustate==='success'){
    //   this.props.socket.emit('online', sessionStorage.name);
    //   history.push('/m')
    // }
    if (this.state.name&&this.state.password){
      console.log('login');
      this.props.socket.emit('login',{name:this.state.name,password:this.state.password})
    }
    else if(this.state.name){
      this.setState({
        passState:'error',
        passMsg:""
      })
    }
    else{
      this.setState({
        userState:'error',
        userMsg:""
      })
    }
  }


  //
  regist(){
    this.setState({
      visible:true
    })
  }
  //
  handleCancel(){
    //
    this.setState({
      visible:false,
      ustate:null,
      msg:null,
      pstate:null,
      pmsg:null
    })
  }
  //
  validateName(event){
    let names=this.state.names;
    let name=event.target.value;
    let regist_name,status,msg=null;
    if (name && names.indexOf(name)<0){//
      //.indexOf()-1
      regist_name=name;
      status='success';
      msg="";
    }
    else if (name){//
      status='error';
      msg="";
    }
    else{//
      msg="";
    };
    this.setState({
        ustate:status,
        msg:msg,
        regist_name:regist_name
    });
  }
  //
  setPassword(event){
    let p=event.target.value;
    this.setState({
      regist_password:p
    })
  }
  //
  confirmPassword(event){
    let p=event.target.value;
    this.setState({
      pstate:null,
      pmsg:null,
      confirm_password:p
    })
  }
  //
  regist_submit(){
    //
    if (this.state.ustate!=='success'){}
    else if (!this.state.regist_password){
      this.setState({
        pstate:'error',
        pmsg:""
      })
    }
    else if (this.state.regist_password!==this.state.confirm_password){
      this.setState({
        pstate:'error',
        pmsg:""
      })
    }
    else{
      this.props.socket.emit('regist',{name:this.state.regist_name,password:this.state.regist_password})
      this.setState({
        visible:false
      })
    }
  }


  componentWillMount() {
    //
    //_isMounted
    //Can't call setState (or forceUpdate) on an unmounted component.
    //warningsetState()
    //state
    //setStatesetState
    this._isMounted = true;
    this.props.socket.on('all_name', (data) => {
          if (this._isMounted) {
            this.setState({
              names:data.data
              });
            }
        }
      );

    this.props.socket.on('login_feedback',(data)=>{
      switch (data.code) {
        case 0:
          sessionStorage.name=data.msg
          this.props.socket.emit('online', sessionStorage.name);
          history.push('/m');
          message.success('')
          break;
        case 1:
          message.error(data.msg)
          if (this._isMounted) {
            this.setState({
              passState:'error',
              passMsg:""
              });
            }
          break;
        case 2:
          message.error(data.msg)
          if (this._isMounted) {
            this.setState({
              userState:'error',
              userMsg:""
              });
            }
          break;
        case 3:
          message.error(data.msg)
          if (this._isMounted) {
            this.setState({
              userState:'error',
              userMsg:""
              });
            }
          break;
        default:
          break;
      }
    })

    this.props.socket.on('regist_feedback',(data)=>{
      switch (data.code) {
        case 0:
          message.error(data.msg)
          break;
        case 1:
          message.success(data.msg)
          break;
        default:
          break;
      }
    })
  }

  componentWillUnmount() {
        this._isMounted = false
  }
  // <FormItem validateStatus={this.state.ustate} required={true}
  //   hasFeedback={true}
  //   help={this.state.msg}>
  render() {
    return (
      <div>
        <Clock />
        <Form style={styles.loginbox} className="login-form">
          <FormItem required={true} validateStatus={this.state.userState}
            hasFeedback={true} help={this.state.userMsg}>
              <Input prefix={<Icon type="user" style={{ fontSize: 13 }} />}
               placeholder="Please input user name" maxLength="40"
               onChange={this.changeName.bind(this)}/>
          </FormItem>
          <FormItem required={true} validateStatus={this.state.passState}
            hasFeedback={true} help={this.state.passMsg}>
              <Input prefix={<Icon type="lock" style={{ fontSize: 13 }} />}
               type="password" placeholder="Password" maxLength="20"
               onChange={this.changePassword.bind(this)}/>
          </FormItem>
          <Col>
            <Button type="primary" className="login-form-button"
            style={{marginRight:50}} onClick={this.submit.bind(this)}>
              
            </Button>
            <Button type="primary" className="login-form-button"
            onClick={this.regist.bind(this)}>
              
            </Button>
          </Col>
        </Form>
        <Modal width='450px' title={''} visible={this.state.visible}
            onCancel={this.handleCancel.bind(this)}
            footer={null}>
            <Form className="login-form">
              <FormItem required={true} validateStatus={this.state.ustate}
                hasFeedback={true} help={this.state.msg} label="">
                  <Input prefix={<Icon type="user" style={{ fontSize: 13 }} />}
                   placeholder="Please input a name" maxLength="40"
                   onChange={this.validateName.bind(this)}/>
              </FormItem>
              <FormItem required={true} label="">
                  <Input prefix={<Icon type="lock" style={{ fontSize: 13 }} />}
                   type="password" placeholder="Please set your password"
                   maxLength="20"
                   onChange={this.setPassword.bind(this)}/>
              </FormItem>
              <FormItem required={true} validateStatus={this.state.pstate}
                hasFeedback={true} help={this.state.pmsg} label="">
                  <Input prefix={<Icon type="lock" style={{ fontSize: 13 }} />}
                   type="password" placeholder="Please set your password"
                   maxLength="20"
                   onChange={this.confirmPassword.bind(this)}/>
              </FormItem>
                <Button type="primary" className="login-form-button"
                 onClick={this.regist_submit.bind(this)}>
                  
                </Button>
            </Form>
        </Modal>
      </div>
    );
  }
}

export default LoginForm
