import { EventEmitter } from 'events';//
import dispatcher from '../dispatcher';//

class RoomMsgStore extends EventEmitter {
    constructor() {
        super();
        this.msgs =[];
    }

    getMsg(room) {
      var temp=[];
      var msgs=this.msgs;
      for (var i=0;i<msgs.length;i++){
          if (msgs[i].room===room){
            temp.push(msgs[i])
          }
        };
      return temp;
    }

    addMsg(data) {
        this.msgs.push(data)
    }

    saveMsg(data){
      this.msgs=data.concat(this.msgs);
      this.emit('push_room_msg');
    }

    send(){
      this.emit('push_room_msg');
    }
    handleActions(action) {
        switch(action.type) {
            case 'ADD_MESSAGE': {
                this.addMsg(action.data);
                break;
            }
            case 'ROOM_MESSAGE':{
              this.saveMsg(action.data);
              break;
            }
            case 'GET_MESSAGE':{
              console.log('send')
              this.send();
              break;
            }
            default:{
              break;
            }
        }
    }
}

const rStore = new RoomMsgStore();
//
dispatcher.register(rStore.handleActions.bind(rStore));
export default rStore;
